.gallery {
    padding:50px 0 30px;

    &__image {
        width:100%;
        height: 0;
        padding-bottom:58%;
        position:relative;
        overflow: hidden;
        

        &:first-of-type {
            padding-bottom:100%;
        }


        img {
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
            width: 100%;
        }
    }
}